@import "src/libs/include-media/dist/include-media";
@import "src/sass/special-version/vspec-variables";
@import "src/sass/utils/functions";
@import "src/sass/utils/mixins";
@import "src/sass/utils/placeholders";

/* special version tools panel */
/* ============================================================ */
.spec-panel {
  display: none;
  position: relative;
  width: nth($spec-panel-size, 2);
  //max-width: #{$spec-text-page}
  max-width: 1000px;
  margin: 0 auto 4rem;
  line-height: $spec-line-height--small;
  text-align: left;
  box-sizing: border-box;
  border: 0 solid currentColor;
  border-width: 0 $spec-border-width $spec-border-width;
  //z-index: 995
  z-index: 145;
  // transition
  $prop: transform, box-shadow;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: 0.2s;
    timing-function: ease-in-out;
  };

  @include media("<vspec-lg") {
    margin-bottom: 3rem;
  }

  @include media("<vspec-md") {
    margin-bottom: 2rem;
  }

  @include media("<vspec-sm") {
    margin-bottom: 1.5rem;
  }

  //+media('<mob-md')
  // margin-bottom: 1rem
  .#{$spec-mod} & {
    display: block;
  }

  &.settings-is-open {
    box-shadow: 0 0 0 4px currentColor;
    //+media('<tab-lg')
    // box-shadow: inset 0 0 0 4px currentColor

  }

  //.#{$spec-mod} &
  // display: block
}

.spec-panel__layout {
  overflow-y: hidden;
  overflow-x: hidden;
  box-sizing: border-box;
  @include spec-bg-gradient;

  &:hover,
  .no-js & {
    overflow-x: auto;
  }
}

.spec-panel__holder {
  display: flex;
  justify-content: flex-start;
  box-sizing: border-box;
  letter-spacing: normal;
  word-spacing: normal;
  //padding: 0 round($spec-panel-inner-space / 2)
  // for ie 9
  .no-flexwrap & {
    display: block;
    padding-top: 10px;
  }

  @include media("<vspec-xs") {
    //padding: 0 round($spec-panel-inner-space--sm / 2)
    padding: 0;
  }
}

.spec-panel__layout,
.spec-panel__holder,
.spec-panel__item,
.spec-panel__footer {
  height: nth($spec-panel-size, 1);
  @include media("<vspec-xs") {
    height: nth($spec-panel-size--sm, 1);
  }
}

.spec-panel__item, .spec-panel__footer {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0 round($spec-panel-inner-space / 2);
  // for ie 9
  .no-flexwrap & {
    display: inline-block;
    vertical-align: top;
  }

  @include media("<vspec-xs") {
    padding: 0 round($spec-panel-inner-space--sm / 2);
  }
}

.spec-panel__footer {
  margin-left: auto;
}

.spec-panel__label {
  // for ie 9
  display: inline-block;
  // for ie 9
  vertical-align: top;
  white-space: nowrap;
  padding-right: round($spec-panel-inner-space / 2);
  //font-family: Arial, Helvetica, sans-serif
  font-family: inherit;
  font-size: 16px;
  line-height: 18px;
  letter-spacing: normal;
  word-spacing: normal;
  word-break: normal;
  @include media("<vspec-md") {
    display: none;
  }

  //+media('<mob-md')
  // padding-right: round($spec-panel-inner-space--sm / 2)
}

.spec-panel__btn-group {
  display: flex;
  align-items: center;
  border-color: currentColor;
  margin-right: round($spec-panel-inner-space / 2);
  // for ie 9
  .no-flexwrap & {
    display: inline-block;
    vertical-align: top;
  }

  @include media("<vspec-xs") {
    margin-right: round($spec-panel-inner-space--sm / 2);
  }

  &:last-child {
    margin-right: 0;
  }
}

.spec-panel__btn {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: $spec-panel__btn-size;
  height: $spec-panel__btn-size;
  margin-right: round($spec-panel-inner-space / 2);
  padding: 2px 0;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: normal;
  word-spacing: normal;
  border-radius: 4px;
  //border: 1px solid $spec-text-color
  border: 1px solid currentColor;
  box-sizing: border-box;
  // reset outline
  outline: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0) !important;
  user-select: none !important;
  // for ie 9
  .no-flexwrap & {
    display: inline-block;
    vertical-align: top;
  }

  &, .#{$spec-mod} & {
    text-decoration: none;
    color: currentColor;
  }

  @include media("<vspec-xs") {
    width: $spec-panel__btn-size--sm;
    height: $spec-panel__btn-size--sm;
    margin-right: round($spec-panel-inner-space--sm / 2);
  }

  &:last-child {
    margin-right: 0;
  }

  & > span {
    // for ie 9
    display: inline-block;
    // for ie 9
    vertical-align: top;
    display: none;
    text-align: left;
    margin-right: 5px;
  }

  & > svg + span {
    margin-right: 0;
    margin-left: 5px;
  }

  //svg
  // display: block
  // position: absolute
  // left: 50%
  // top: 50%
  // transform: translate(-50%, -50%)
  // box-sizing: border-box
  svg {
    display: inline-block;
    // for ie 9
    vertical-align: top;
    box-sizing: border-box;
    @include media("<vspec-xs") {
      //transform: translate(-50%, -50%) scale(0.85)
      transform: scale(0.85);
    }
  }

  path {
    fill: currentColor;
  }

  // states of special buttons
  @include spec-button-states;
}

.spec-panel__btn_color_def {
  &, .#{$spec-mod} & {
    color: $spec-text-color;
    background-color: $spec-bg-color;
  }
}

.spec-panel__btn_color_white {
  &, .#{$spec-mod} & {
    color: $spec-text-color--scheme-wb;
    background-color: $spec-bg-color--scheme-wb;
  }
}

.spec-panel__btn_color_blue {
  &, .#{$spec-mod} & {
    color: $spec-text-color--scheme-blue;
    background-color: $spec-bg-color--scheme-blue;
  }
}

.spec-panel__btn_color_brown {
  &, .#{$spec-mod} & {
    color: $spec-text-color--scheme-brown;
    background-color: $spec-bg-color--scheme-brown;
  }
}

.spec-panel__btn_color_green {
  &, .#{$spec-mod} & {
    color: $spec-text-color--scheme-green;
    background-color: $spec-bg-color--scheme-green;
  }
}

$icon-spacing--size: 16px 2px;
$icon-spacing--space: 3px;

.spec-panel__btn_spacing_sm, .spec-panel__btn_spacing_md, .spec-panel__btn_spacing_lg {
  .icon {
    &, &::before, &::after {
      display: block;
      position: absolute;
      left: 50%;
      top: 50%;
      height: nth($icon-spacing--size, 1);
      width: nth($icon-spacing--size, 2);
      background-color: currentColor;
    }

    & {
      margin: -#{round(nth($icon-spacing--size, 1) / 2)} 0 0 -#{round(nth($icon-spacing--size, 2) / 2)};
      @include media("<vspec-xs") {
        transform: scale(0.85);
      }
    }

    &::before, &::after {
      content: "";
      margin-top: -#{round(nth($icon-spacing--size, 1) / 2)};
    }

    &::before {
      margin-left: -#{$icon-spacing--space + round(nth($icon-spacing--size, 2) * 1.5)};
    }

    &::after {
      margin-left: #{$icon-spacing--space + round(nth($icon-spacing--size, 2) * 0.5)};
    }
  }
}

.spec-panel__btn_spacing_md {
  .icon {
    $icon-spacing--space: 6px;

    &::before {
      margin-left: -#{$icon-spacing--space + round(nth($icon-spacing--size, 2) * 1.5)};
    }

    &::after {
      margin-left: #{$icon-spacing--space + round(nth($icon-spacing--size, 2) * 0.5)};
    }
  }
}

.spec-panel__btn_spacing_lg {
  .icon {
    $icon-spacing--space: 9px;

    &::before {
      margin-left: -#{$icon-spacing--space + round(nth($icon-spacing--size, 2) * 1.5)};
    }

    &::after {
      margin-left: #{$icon-spacing--space + round(nth($icon-spacing--size, 2) * 0.5)};
    }
  }
}

// Setting btn
.spec-panel__btn_settings, .spec-panel__btn_search, .spec-panel__btn_switcher {
  @include media(">=vspec-lg") {
    width: auto;
    padding: 2px 0.5em;
    //padding-left: 40px
    //padding-right: 10px
    //svg
    // left: 5px
    // transform: translate(0, -50%)
    & > span {
      // for ie 9
      display: inline-block;
      white-space: nowrap;
    }
  }
}

// Special Version Advanced Settings
.spec-drop {
  //+spec-reset-bg
  overflow: hidden;
  display: none;
}

.spec-drop__layout {
  padding: $spec-panel-inner-space;
  border-top: $spec-border-width solid currentColor;
  @include media("<vspec-lg") {
    padding: 0;
  }
}

.spec-drop__title {
  font-weight: bold;
  word-wrap: break-word;
  margin-bottom: round($spec-panel-inner-space / 2);
  @include spec-font--title;
  @include media("<vspec-lg") {
    padding: 7px $spec-panel-inner-space--sm 8px;
    margin-bottom: 0;
    font-size: 1.2rem;
    line-height: 1.4rem;
    letter-spacing: 0.08em;
    word-spacing: normal;
    font-weight: normal;
    text-align: center;
    text-transform: uppercase;
    border: 0 solid currentColor;
    border-width: 1px 0;
    @include spec-bg-gradient;
  }

  //+media('<mob-md')
  // margin-bottom: round($spec-panel-inner-space--sm / 2)
  .spec-drop__item + & {
    margin-top: round($spec-panel-inner-space * 2);
    @include media("<vspec-lg") {
      margin: 0;
    }

    //+media('<mob-md')
    // margin-bottom: round($spec-panel-inner-space--sm * 2)

  }
}

.spec-drop__item, .spec-drop__footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  margin: round($spec-panel-inner-space / 2) 0 0;
  letter-spacing: normal;
  word-spacing: normal;
  // for ie 9
  .no-flexwrap & {
    display: block;
  }

  @include media("<vspec-lg") {
    //display: block
    margin: 0;
    padding-top: 5px;
  }

  //+media('<mob-md')
  // margin: round($spec-panel-inner-space--sm / 2) 0 0
  .spec-drop__title + & {
    margin-top: 0;
  }
}

.spec-drop__footer {
  margin-top: round($spec-panel-inner-space * 2);
  //margin-top: $spec-panel-inner-space
  padding-top: $spec-panel-inner-space;
  border-top: $spec-border-width solid currentColor;
  justify-content: flex-end;
  @include media("<vspec-lg") {
    display: flex;
    justify-content: flex-start;
    margin-top: 0;
    padding: 5px 0;
  }

  //+media('<mob-md')
  // margin-top: round($spec-panel-inner-space--sm * 2)
  // padding-top: $spec-panel-inner-space--sm
  .spec-drop__btn-group {
    @include media("<vspec-lg") {
      margin: 0;
    }
  }
}

.spec-drop__label {
  // for ie 9
  display: inline-block;
  // for ie 9
  vertical-align: top;
  margin: round($spec-panel-inner-space / 2);
  margin-left: 0;
  font-size: spec-heading(h3, "font-size");
  line-height: spec-heading(h3, "line-height");
  word-wrap: break-word;
  @include media("<vspec-lg") {
    display: block;
    width: 100%;
    padding: 0 $spec-panel-inner-space--sm;
    margin: 0;
    font-size: 13px;
    line-height: 19px;
    letter-spacing: 0.05em;
    word-spacing: normal;
    text-align: center;
    text-transform: uppercase;
  }
}

.spec-drop__btn-group {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: stretch;
  margin-right: round($spec-panel-inner-space / 2);
  // for ie 9
  .no-flexwrap & {
    display: inline-block;
    vertical-align: top;
  }

  @include media("<vspec-lg") {
    padding: 0 round($spec-panel-inner-space--sm / 2);
    margin: 0;
  }

  &:last-child {
    margin-right: 0;
  }
}

.spec-drop__btn {
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: round($spec-panel-inner-space / 2);
  margin-left: 0;
  padding: 8px 0.5em;
  //+spec-font-size--normal
  //letter-spacing: normal
  //word-spacing: normal
  min-height: 50px;
  border-radius: 4px;
  box-sizing: border-box;
  border: 1px solid currentColor;
  // reset outline
  outline: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0) !important;
  user-select: none !important;
  // for ie 9
  .no-flexwrap & {
    display: inline-block;
    vertical-align: top;
  }

  @include media("<vspec-lg") {
    font-size: 16px;
    line-height: 20px;
    min-height: 0;
    border-radius: 10px;
    //margin: 5px
    padding: 10px;
  }

  @include media("<vspec-xs") {
    margin: 5px;
  }

  &, .#{$spec-mod} & {
    text-decoration: none;
    color: currentColor;
  }

  //+media('<mob-md')
  // margin: round($spec-panel-inner-space--sm / 2)
  // margin-left: 0
  span {
    // for ie 9
    display: inline-block;
    // for ie 9
    vertical-align: top;
  }

  svg {
    display: inline-block;
    // for ie 9
    vertical-align: top;
    box-sizing: border-box;
    margin-right: 5px;
    @include media("<vspec-lg") {
      width: 20px;
      height: 20px;
    }
  }

  path {
    fill: currentColor;
  }

  .icon {
    display: none;
  }

  // states of special buttons
  @include spec-button-states;
}

